* {
  box-sizing: border-box;
}

.App {
  text-align: center;
  padding: 20px;
  background-color: white;
}

.App.dark-mode {
  background-color: #1a1a1a;
  color: rgb(92, 66, 66);
}

.nav {
  background-color: #9dfc7c;
  color: black;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  gap: 2rem;
  padding: 0 1rem;
}

.nav .site-title {
  font-size: 2rem;
}

.nav ul {
  padding: 0; /* rid default padding-left */
  margin: 0; /* rid default margins top&bottom */
  list-style: none;
  display: flex;
  gap: 2rem;
}

.nav a {
  text-decoration: none;
  color: inherit;
  height: 100%; 
  display: flex;
  align-items: center;
  padding: 0.25rem;
}

.nav li:hover {
  background-color: #777;
}




header {
  background-color: #fa7fee;
  padding: 20px 10px;
  margin-bottom: 20px;
}

header h1 {
  font-family: mabry;
  font-size: 2.5em;
}

header h3 {
  font-family: mabry;
  font-size:  1.5em;
}

section {
  background-color: #FFF066;
  margin-bottom: 30px;
  padding: 10px;
}

.project-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
  padding: 0 20px;
}

.project-card {
  background-color:	#A8A6FF;
  padding: 20px;
  border-radius: 4px;
  min-width: 250px;
  transition: transform 0.2s;
}

.project-card:hover {
  transform: translateY(-5px);
}

.contact {
 background-color: #918efa;
 padding: 40px;
 color: #383838;
}

footer {
  margin-top: 60px;
  color: #666;
}
